.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}

.sr-only-focusable:active, .sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto;
}

/*----------------------------------------------*/
/*Variables*/
/*----------------------------------------------*/
/*----------------------------------------------*/
/* In Context */
/*----------------------------------------------*/
.lt-ie10 #site-canvas {
  display: none;
}

.lt-ie10 #nocando {
  max-width: 440px;
  padding: 32px;
  margin: 200px auto;
  background: #ffffff;
}

.lt-ie10 #nocando h1 {
  margin-bottom: 32px;
  font-size: 24px;
  line-height: 1.1;
}

/*----------------------------------------------*/
/*Fixing Default CSS*/
/*----------------------------------------------*/
body, ul, li, ol, p {
  font-family: 'Hind', sans-serif;
  font-weight: 400;
  line-height: 1.4em;
  color: #606161;
}

html, body {
  font-size: 100%;
}

ul {
  margin-bottom: .5em;
  list-style: none;
}

body {
  background-color: #f2f2f2;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

::-moz-selection {
  /* Code for Firefox */
  background: #f2ecb7;
}

::selection {
  background: #f2ecb7;
}

.centerme {
  text-align: center;
}

.inline {
  display: inline-block;
}

#site-canvas {
  z-index: -1;
  max-width: 100%;
  margin-right: auto;
  margin-left: auto;
}

/*----------------------------------------------*/
/* Text  Styles*/
/* weights: 300 thin, 400 regular, 700 bold, 900 black */
/*----------------------------------------------*/
a {
  color: #71a333;
  text-decoration: none;
  transition: color .2s;
}

a:hover {
  color: #a3d16b;
}

.title h1 {
  display: inline-block;
  margin: 0;
  font-family: 'Hind';
  font-size: 2em;
  font-weight: 700;
  line-height: 1em;
  color: #606161;
}

.titlewhite {
  display: inline-block;
  margin: 0;
  font-family: 'Hind';
  font-size: 2em;
  font-weight: 700;
  line-height: 1em;
  color: white;
}

.titlewhite p {
  display: inline-block;
  padding-top: 1em;
  font-family: 'Hind';
  font-size: 1em;
  font-weight: 700;
  line-height: 1em;
  color: white;
}

@media screen and (max-width: 712px) {
  .titlewhite p {
    font-size: .9em;
  }
}

.section-baseline {
  border-bottom: 1px solid #c6c7c7;
}

.section-header {
  margin-top: 1em;
  margin-bottom: 1em;
  text-align: center;
}

.feature-header {
  display: block;
  padding-top: 195px;
  font-size: 2.5em;
  color: #ffffff;
  text-shadow: 1px 1px #606161;
}

.dark-overlay {
  position: absolute;
  display: block;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.2);
}

.section-headerwelcome {
  width: 60%;
  padding-top: 8em;
  margin: auto;
  text-align: center;
}

.section-headerwelcome .sub-head {
  font-size: 1.2em;
}

.section-headerwelcome .sub-head p {
  max-width: 800px;
  margin: .5em auto .2em auto;
  font-size: .9em;
  color: #797b7b;
}

.section-headerwelcome .btn {
  margin: 10px 14px 6px 6px;
}

@media screen and (max-width: 880px) {
  .section-headerwelcome {
    width: 75%;
  }
}

@media screen and (max-width: 712px) {
  .section-headerwelcome {
    font-size: .8em;
  }
}

@media screen and (max-width: 565px) {
  .section-headerwelcome {
    width: 80%;
  }
}

@media screen and (max-width: 450px) {
  .section-headerwelcome {
    padding-top: 4.5em;
  }
}

.section-headersat {
  position: relative;
  top: 60px;
  right: 200px;
  left: 0;
  margin-top: 1.5em;
  margin-bottom: 1em;
  text-align: center;
}

@media screen and (max-width: 800px) {
  .section-headersat {
    margin-bottom: 50px;
  }
}

.section-headersat .titlewhite {
  font-size: 24px;
}

.sub-head {
  padding: 0 0 .75em 0;
  font-family: 'Lato';
  font-size: 1.6em;
  font-weight: 400;
  letter-spacing: .01em;
}

.sub-head p {
  padding: .5em 0 .5em 0;
  margin-right: 1.5em;
  margin-left: 1.5em;
  font-family: 'Lato';
  font-size: 1em;
  font-weight: 400;
  letter-spacing: .01em;
}

.textbox {
  padding-bottom: .5em;
  font-size: 1.1em;
  font-weight: 400;
  line-height: 1.6em;
}

.textbox p {
  padding-bottom: .5em;
  font-size: 1.1em;
  font-weight: 300;
  line-height: 1.6em;
  text-align: left;
}

.textbox li {
  margin-bottom: .4em;
}

.textbox ol {
  margin-left: 20px;
}

.orderedlist-styled {
  list-style-type: decimal;
}

/*----------------------------------------------*/
/*Container Styles*/
/*----------------------------------------------*/
.container {
  max-width: 800px;
  padding-top: 1.5em;
  padding-bottom: 1.5em;
  margin-right: auto;
  margin-left: auto;
}

.sub-container {
  border: #dbdbda solid 1px;
}

.sectionheader {
  padding-top: 32px;
  padding-bottom: 32px;
}

.welcome-container {
  background: url("/assets/graphics/content/process/Stars.svg");
}

.events-container .section-header {
  color: #ffffff;
}

.contribute-base {
  margin-top: .8em;
}

.text-box {
  width: 400px;
  padding: 14px;
}

.badge-box {
  width: 400px;
  height: 100%;
  padding: 14px;
}

.line-break {
  width: 60%;
  height: 2px;
  margin-top: 4px;
  margin-bottom: 8px;
  background-color: #dbdbda;
}

.center-text {
  text-align: center;
}

/*----------------------------------------------*/
/*Nav Bar Style*/
/*----------------------------------------------*/
header {
  position: absolute;
  top: 0;
  z-index: 1;
  display: inline-block;
  width: 100%;
  height: 52px;
  color: #dbdbda;
  background-color: #303030;
}

header ul {
  display: inline-block;
}

header li {
  display: inline-block;
  margin: 0;
}

.navbar-background {
  max-width: 1100px;
  margin-right: auto;
  margin-left: auto;
}

.nav-icon {
  float: left;
  padding-top: 6px;
  padding-left: 2em;
}

@media screen and (min-width: 1100px) {
  .nav-icon {
    padding-left: 0;
  }
}

.nav-list {
  position: relative;
  display: inline-block;
  float: right;
}

@media screen and (max-width: 560px) {
  .nav-list {
    display: none;
  }
}

.nav-input {
  position: relative;
  bottom: 3px;
  display: inline-block;
  float: right;
  padding: 0 16px 0 40px;
}

.nav-item {
  height: 52px;
  padding: 0 10px 0 7.2px;
  font-family: 'Lato';
  font-size: .9em;
  line-height: 52px;
  color: white;
  color: #dbdbda;
  letter-spacing: .06em;
  cursor: pointer;
  transition: color .2s;
}

.nav-item:hover {
  color: #696966;
}

.nav-links {
  float: center;
}

.inner {
  max-width: 1100px;
  padding-right: 2em;
  padding-left: 2em;
  margin-right: auto;
  margin-left: auto;
}

.nav-dropdown {
  position: relative;
  display: inline-block;
}

.dropdown-content {
  position: absolute;
  right: 0;
  z-index: 1;
  height: 0;
  overflow: hidden;
  opacity: 0;
  text-align: right;
  transition: opacity .3s;
}

.dropdown-content :hover a {
  color: #696966;
}

.dropdown-content .nav-item {
  padding-left: 12px;
  background-color: #303030;
  border-bottom: 1px solid #565656;
}

.nav-events {
  width: 180px;
}

.nav-dropdown:hover .dropdown-content {
  height: auto;
  opacity: 1;
}

